@import "variables/variables.scss";

.for-fun {
  padding: 50px 1rem;
}

.text-center{
  text-align: center;
}

.funBtn {
  background: #140518;
  color: #f7f7f7;
  font-family: 'Museo Moderno', sans-serif;
  font-size: 20px;
  padding: 10px 20px;
  border: solid 2px #f7f7f7;
  border-radius: 5px;
  cursor: pointer;
}

.for_fun_blinking {
    background: #06474569;
    font-family: $home_links;
    font-size: 40pt;
    text-decoration: none;
    color: $home1;
    text-shadow: 0 0 0.6rem $projects_text, 0 0 1.5rem #ff6565,
    -0.2rem 0.1rem 1rem #ff6565, 0.2rem 0.1rem 1rem #ff6565,
    0 -0.5rem 2rem #ff2424, 0 0.5rem 3rem #ff2424;
  animation: shine 4s forwards, flicker 2s infinite;
}



@keyframes blink {
  0%,
  22%,
  36%,
  75% {
    color: #e6eaff;
    text-shadow: 0 0 0.6rem #e6eaff, 0 0 1.5rem #657cff,
      -0.2rem 0.1rem 1rem #6581ff, 0.2rem 0.1rem 1rem #657cff,
      0 -0.5rem 2rem #2457ff, 0 0.5rem 3rem #247cff;
  }
  28%,
  33% {
    color: #2b52ff;
    text-shadow: none;
  }
  82%,
  97% {
    color: $home_txt;
    text-shadow: none;
  }
}

.flicker {
  animation: shine 2s forwards, blink 3s 2s infinite;
}

.fast-flicker {
  animation: shine 2s forwards, blink 10s 1s infinite;
}

@keyframes shine {
  0% {
    color: #183c6b;
    text-shadow: none;
  }
  100% {
    color: #e6f5ff;
    text-shadow: 0 0 0.6rem #e6fbff, 0 0 1.5rem #659bff,
      -0.2rem 0.1rem 1rem #656fff, 0.2rem 0.1rem 1rem #6581ff,
      0 -0.5rem 2rem #3324ff, 0 0.5rem 3rem #2824ff;
  }
}

@keyframes flicker {
  from {
    opacity: 1;
  }

  4% {
    opacity: 0.9;
  }

  6% {
    opacity: 0.85;
  }

  8% {
    opacity: 0.95;
  }

  10% {
    opacity: 0.9;
  }

  11% {
    opacity: 0.922;
  }

  12% {
    opacity: 0.9;
  }

  14% {
    opacity: 0.95;
  }

  16% {
    opacity: 0.98;
  }

  17% {
    opacity: 0.9;
  }

  19% {
    opacity: 0.93;
  }

  20% {
    opacity: 0.99;
  }

  24% {
    opacity: 1;
  }

  26% {
    opacity: 0.94;
  }

  28% {
    opacity: 0.98;
  }

  37% {
    opacity: 0.93;
  }

  38% {
    opacity: 0.5;
  }

  39% {
    opacity: 0.96;
  }

  42% {
    opacity: 1;
  }

  44% {
    opacity: 0.97;
  }

  46% {
    opacity: 0.94;
  }

  56% {
    opacity: 0.9;
  }

  58% {
    opacity: 0.9;
  }

  60% {
    opacity: 0.99;
  }

  68% {
    opacity: 1;
  }

  70% {
    opacity: 0.9;
  }

  72% {
    opacity: 0.95;
  }

  93% {
    opacity: 0.93;
  }

  95% {
    opacity: 0.95;
  }

  97% {
    opacity: 0.93;
  }

  to {
    opacity: 1;
  }
}